@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'Indie Flower'
}

.page {
    background-color: #ccc;
    margin-top: 30%;
    text-align: center;
    padding: 20px 0 2px 0;
    color: #fff;
}

h1 {
    text-align: center;
    /* margin: 40px; */
    color: #000;
    position: absolute;
    top: 22%;
    /* left: 20%; */
    font-size: 66px;
    font-family: 'Times New Roman';
    width: 100%;
}

button {
    margin: auto;
    background-color: #ccc;
    color: #ffffff;
    display: block;
    position: absolute;
    z-index: 9;
    bottom: 7%;
    left: 40%;
    width: 20%;
    padding: 10px;
    font-size: 24px;
    line-height: 30px;
    border: outset;
    /* border-radius: 9px; */
}

button:hover {
    background-color: #7e7e7e;
}

p {
    font-size: 18px;
}

form {
    margin: 15px 0 30px;
}

.radio {
    margin: 5px 10px;
    position: relative;
    display: inline-flex;
}

.radio:first-child:after {
    display: block;
    position: absolute;
    width: 40px;
    content: '1';
    line-height: 40px;
    background-color: rgba(0, 0, 0, 1);
    height: 40px;
    border: solid 2px #333;
    border-radius: 3px;
    font-weight: 600;
}

.radio:nth-child(2):after {
    display: block;
    position: absolute;
    width: 40px;
    content: '2';
    line-height: 40px;
    background-color: rgba(0, 0, 0, 1);
    height: 40px;
    border: solid 2px #333;
    border-radius: 3px;
    font-weight: 600;
}

.radio:nth-child(3):after {
    display: block;
    position: absolute;
    width: 40px;
    content: '3';
    line-height: 40px;
    background-color: rgba(0, 0, 0, 1);
    height: 40px;
    border: solid 2px #333;
    border-radius: 3px;
    font-weight: 600;
}

.radio:nth-child(4):after {
    display: block;
    position: absolute;
    width: 40px;
    content: '4';
    line-height: 40px;
    background-color: rgba(0, 0, 0, 1);
    height: 40px;
    border: solid 2px #333;
    border-radius: 3px;
    font-weight: 600;
}

.radio.chosen:after{
    background-color: red;
}

.car-label {
    margin: 10px;
}

.car-label label {
    margin-right: 15px;
}

.car-label select {
    padding: 3px;
    border-radius: 3px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

